import './App.css';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import React, { Component } from 'react';
import  Home  from './views/home/home';
import {
  ConnectionProvider,
  WalletProvider,
} from "@solana/wallet-adapter-react";
import { WalletDialogProvider } from "@solana/wallet-adapter-material-ui";
import { clusterApiUrl } from "@solana/web3.js";
import {
  getPhantomWallet,
  getSlopeWallet,
  getSolflareWallet,
  getSolletWallet,
  getSolletExtensionWallet,
} from "@solana/wallet-adapter-wallets";
import { WalletAdapterNetwork } from "@solana/wallet-adapter-base";
import { createTheme, ThemeProvider } from "@material-ui/core";

const network = process.env.REACT_APP_SOLANA_NETWORK;
const theme = createTheme({
  palette: {
      type: 'dark',
  },
  overrides: {
      MuiButtonBase: {
          root: {
              justifyContent: 'flex-start',
          },
      },
      MuiButton: {
          root: {
              textTransform: undefined,
              padding: '12px 16px',
          },
          startIcon: {
              marginRight: 8,
          },
          endIcon: {
              marginLeft: 8,
          },
      },
  },
});

class App extends Component {

  constructor(props) {
    super(props);
    this.state = {
      endpoint: clusterApiUrl(network),
      wallets: [
        getPhantomWallet(),
        getSlopeWallet(),
        getSolflareWallet(),
        getSolletWallet({ network }),
        getSolletExtensionWallet({ network })
      ]
    }
  }
  
  render() {
    return (
      <ThemeProvider theme={theme}>
        <ConnectionProvider endpoint={this.state.endpoint}>
          <WalletProvider wallets={this.state.wallets} autoConnect>
            <WalletDialogProvider>
              <BrowserRouter>
                <div>
                  <Switch>
                      <Route path="/" component={Home} exact/>
                      <Route path="/soldout" component={Home}/>
                      <Route path="/comingsoon" component={Home}/>
                      <Route path="/during-minting" component={Home}/>
                      <Route path="*" component={Home} />
                    </Switch>
                </div>
              </BrowserRouter>
            </WalletDialogProvider>
          </WalletProvider>
        </ConnectionProvider>
      </ThemeProvider>
    );
  }
}

export default App;
