import { IMAGES } from "../../constants/image_constants";
import React, { useState, useEffect } from "react";
import { useAnchorWallet } from "@solana/wallet-adapter-react";
import {
  CandyMachine,
  awaitTransactionSignatureConfirmation,
  getCandyMachineState,
  mintOneToken,
} from "../../views/home/candy-machine";
import * as anchor from "@project-serum/anchor";

const candyMachineId = new anchor.web3.PublicKey(
  process.env.REACT_APP_CANDY_MACHINE_ID
);
 
const config = new anchor.web3.PublicKey(
  process.env.REACT_APP_CANDY_MACHINE_CONFIG
);


const connection = new anchor.web3.Connection(process.env.REACT_APP_SOLANA_RPC_HOST);
 const startDateSeed = parseInt(process.env.REACT_APP_CANDY_START_DATE, 10);
const treasury = new anchor.web3.PublicKey(
  process.env.REACT_APP_TREASURY_ADDRESS
);
const txTimeout = 30000; // milliseconds (confirm this works for your project)

const DuringMinting = () => {
  const [quantity, setQuantity,itemsRemaining] = useState(0);
  const [isMinting, setIsMinting] = useState(false);
  const [soldOut,setIsSoldOut] =useState(false)
  const [alertState, setAlertState] = useState({
    open: false,
    message: "",
    severity: "success"
  });

  const IncrementItem = () => {
    if(quantity < 9) setQuantity(quantity + 1);
  };

  const DecreaseItem = () => {
    if(quantity > 0) setQuantity(quantity - 1);
  };

  const handleChange = (event) => {
    setQuantity(event.target.value*1);
  };

  const [candyMachine, setCandyMachine] = useState({
    id: null,
    connection: null,
    program: null
  });
  const wallet = useAnchorWallet();

  useEffect(() => {
    (async () => {

     
      if (!wallet) return;

      const { candyMachine, goLiveDate, itemsRemaining } =
        await getCandyMachineState(
          wallet,
          candyMachineId,
          connection
        );
         
        
        setIsSoldOut(itemsRemaining === 0);
      // setStartDate(goLiveDate);
      setCandyMachine(candyMachine);
    })();
  }, [wallet]);

  const onMint = async () => {
    try {
      console.log(wallet.publicKey.toBase58());
      setIsMinting(true);
      if (wallet && candyMachine?.program) {

        
      
       
        const mintTxId = await mintOneToken(
          candyMachine,
          config,
          wallet.publicKey,
          treasury
        );

       
        console.log(mintTxId);

        const status = await awaitTransactionSignatureConfirmation(
          mintTxId,
          txTimeout,
          connection,
          "singleGossip",
          false
        );
        console.log(status);
        if (!status?.err) {

          alert("Congratulations! Mint succeeded!");
          setAlertState({
            open: true,
            message: "Congratulations! Mint succeeded!",
            severity: "success",
          });
        } else {
          alert("Mint failed! Please try again!");
          setAlertState({
            open: true,
            message: "Mint failed! Please try again!",
            severity: "error",
          });
        }
      }
    } catch (error) {
      //TODO: blech:

     
      let message = error.msg || "Minting failed! Please try again!";
      if (!error.msg) {
        if (error.message.indexOf("0x138")) {
        } else if (error.message.indexOf("0x137")) {
           message = `SOLD OUT!`;
        } else if (error.message.indexOf("0x135")) {
           message = `Insufficient funds to mint. Please fund your wallet.`;
        }
      } else {
        if (error.code === 311) {
           message = `SOLD OUT!`;
           setIsSoldOut(true);
        } else if (error.code === 312) {
           message = `Minting period hasn't started yet.`;
        }
      }

      console.log(message);
      alert(message);
      setAlertState({
        open: true,
        message,
        severity: "error",
      });
    } finally {
      if (wallet) {
        const balance = await connection.getBalance(wallet.publicKey);
        //setBalance(balance / LAMPORTS_PER_SOL);
      }
      setIsMinting(false);
    }
  };

  return (
      <React.Fragment>
        
        <div
          className="d-flex during"
          data-aos="fade-up"
          data-aos-duration="2500"
          data-aos-once="true"
        >
          
          <button className="minus" onClick={DecreaseItem}>
            {" "}
            <img src={IMAGES.minusImage} alt="" className="img-fluid" />{" "}
          </button>
          <div id="number">
            <input
              id="count"
              type="text"
              value={quantity}
              onChange={handleChange}
            />
          </div>
          <button className="plus" onClick={IncrementItem}>
            {" "}
            <img src={IMAGES.plusImage} alt="" className="img-fluid" />{" "}
          </button>
        </div>
        <div
          className="mt-lg-5 mt-4"
          data-aos="fade-up"
          data-aos-duration="2500"
          data-aos-once="true"
        >
          <button className="minnow min" onClick={onMint}>Mint Now</button>
        </div>
        <div className="banner-text text-center text-white">
          <h2>
            Curated, Neural Network Powered, Community Generated Abstract Art
          </h2>
        </div>
      </React.Fragment>
  );
}

export default DuringMinting;
