import React from "react";
import moment from "moment";
import DuringMinting from "../during_minting/during_minting";

class Countdown extends React.Component {
  state = {
    days: undefined,
    hours: undefined,
    minutes: undefined,
    seconds: undefined,
    quantity: 1,
    show: true,
    max: 5,
    min: 0,
    isCountDownComplete: false,
  };

  componentDidMount() {
    const countdown = this.runTheCounter();
    const isCountDownComplete = this.checkTheCountdown(countdown);
    if (isCountDownComplete) {
      this.setState({ isCountDownComplete: true });
    } else {
      this.updateTheCountdownState(countdown);
      this.interval = setInterval(() => {
        const countdown = this.runTheCounter();
        const isCountDownComplete = this.checkTheCountdown(countdown);
        if (isCountDownComplete) {
          this.setState({ isCountDownComplete: true });
          clearInterval(this.interval);
        } else {
          this.updateTheCountdownState(countdown);
        }
      }, 1000);
    }
  }

  runTheCounter() {

    console.log(this.props);
    const { timeTillDate, timeFormat } = this.props;
    const then = moment(timeTillDate, timeFormat);
    const now = moment();
    const duration = moment.duration(then.diff(now));

    console.log("duration");
    console.log(duration);
    const days = parseInt(duration.asDays());
    const hours = parseInt(duration.hours());
    const minutes = parseInt(duration.minutes());
    const seconds = parseInt(duration.seconds());
    return { days, hours, minutes, seconds };
  }

  updateTheCountdownState(countdown) {
    const days = countdown.days;
    const hours = countdown.hours;
    const minutes = countdown.minutes;
    const seconds = countdown.seconds;
    this.setState({ days, hours, minutes, seconds });
  }

  checkTheCountdown(countdown) {
    if (
      countdown.days <= 0 &&
      countdown.hours <= 0 &&
      countdown.minutes <= 0 &&
      countdown.seconds <= 0
    ) {
      return true;
    }
    return false;
  }

  componentWillUnmount() {
    if (this.interval) {
      clearInterval(this.interval);
    }
  }

  ToggleClick = () => {
    this.setState({
      show: !this.state.show,
    });
  };

  handleChange = (event) => {
    this.setState({ quantity: event.target.value });
  };

  render() {
    const { days, hours, minutes, seconds, isCountDownComplete } = this.state;
    return (
      <React.Fragment>
        {isCountDownComplete ? (
          <DuringMinting />
        ) : (
          <React.Fragment>
            
            <div className="count-down-wrapper px-4 py-3">
              <div className="d-flex  justify-content-center align-items-center">
                <div className="col-3 pl-pr text-center">
                  <div className="number">{days}</div>
                  <div className="text">Days</div>
                </div>
                <div className="col-3 pl-pr text-center">
                  <div className="number">{hours}</div>
                  <div className="text">Hours</div>
                </div>
                <div className="col-3 pl-pr text-center">
                  <div className="number">{minutes}</div>
                  <div className="text">Minutes</div>
                </div>
                <div className="col-3 pl-pr text-center">
                  <div className="number">{seconds}</div>
                  <div className="text">Seconds</div>
                </div>
              </div>
            </div>
            <div className="banner-text text-center text-white">
              <h2>
                Curated, Neural Network Powered, Community Generated Abstract
                Art
              </h2>{" "}
            </div>
          </React.Fragment>
        )}
      </React.Fragment>
    );
  }
}

export default Countdown;
